*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url("https://templatemo.com/templates/templatemo_577_liberty_market/assets/images/dark-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  font-family: "Roboto", sans-serif;
  min-height: 200vh;
}

.App {
  min-height: 100vh;
}

::selection {
  background-color: #7453fc;
}

.container {
  width: 100%;
  padding-inline: 3rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 60rem) {
  .container {
    width: 80vw;
  }
}
