.Gallery {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(300px, 1fr)
  );
  gap: 2rem;
}

.Fig {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

.Banner {
  font-size: 15px;
  background-color: #7453fc;
  color: #fff;
  font-weight: 500;
  padding: 5px 15px;
  display: inline-block;
  position: absolute;
  border-radius: 16px;
  left: 50%;
  transform: translateX(-50%);
  top: -16px;
  font-style: italic;
}
@media (min-width: 60rem) {
  .Double {
    grid-column: span 2;
  }
  .Fig {
    flex-direction: row;
  }
}
