.item {
  background-color: #282b2f;
  border: 1px solid #404245;
  padding: 30px;
  border-radius: 20px;
  position: relative;
  margin-bottom: 52px;
}
.item span {
  color: #fff;
  display: inline-block;
  font-size: 15px;
}
.item span.author {
  display: inline-flex;
  width: 100%;
}
.item span.author img {
  margin: 0 auto;
  position: relative;
  z-index: 2;
  width: 100%;
  overflow: hidden;
}
img.shower {
  height: 280px;
}
.item img {
  margin-top: -32px;
  position: relative;
  z-index: 1;
  width: 100%;
  overflow: hidden;
}

.item h4 {
  font-size: 20px;
  margin-top: 30px;
  color: #fff;
}
.item .line-dec {
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  margin: 20px 0px;
}
.item .main-button {
  margin-top: 20px;
  margin-bottom: -60px;
  text-align: center;
}
.main-button a {
  font-size: 14px;
  color: #fff;
  background-color: #7453fc;
  border: 1px solid #7453fc;
  padding: 12px 30px;
  display: inline-block;
  border-radius: 25px;
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: 0.5px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  text-decoration: none;
}

.main-button a:hover {
  background-color: #fff;
  color: #7453fc;
  font-weight: bold;
}
