.DiscoverItems h2 {
  line-height: 36px;
  font-size: 30px;
  font-weight: 700;
  text-transform: capitalize;
  color: #fff;
  margin-bottom: 2rem;
}

h2::before {
  content: "";
  width: 100px;
  height: 2px;
  background-color: #7453fc;
  position: absolute;
  transform: translateY(-1.6rem);
}
h2 em {
  color: #7453fc;
  font-style: normal;
}
.DiscoverItems select,
input {
  width: 100%;
  height: 46px;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 23px;
  background-color: transparent;
  padding: 0px 15px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;

  border: 1px solid #ced4da;
  border-radius: 0.85rem;
  transition: border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
  margin-bottom: 0.9rem;

  appearance: none;
}

.DiscoverItems option {
  background-color: #24242e;
  color: white;
}
.DiscoverItems button {
  font-size: 14px;
  color: #fff;
  background-color: #7453fc;
  border: 1px solid #7453fc;
  height: 46px;
  line-height: 46px;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 25px;
  font-weight: 500;
  text-transform: capitalize;
  letter-spacing: 0.5px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
}

.DiscoverItems button:hover {
  background-color: white;
  color: #7453fc;
}

@media (min-width: 60rem) {
  .Heading {
    display: flex;
    gap: 5vw;
  }

  .Heading form {
    display: flex;
    gap: 1rem;
    flex-grow: 1;
  }
}
