.Landing {
  background-image: url("https://templatemo.com/templates/templatemo_577_liberty_market/assets/images/heading-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding-block: 15rem 10rem;
  text-align: center;
  background-color: #2a2a2a;
  color: white;
}

.Landing h6 {
  font-size: 20px;
  margin-bottom: 15px;
  font-weight: 500;
  letter-spacing: 0.5px;
}
.Landing h2 {
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  line-height: 66px;
  text-transform: uppercase;
}

.Landing span {
  color: #8a75da;
  font-size: 15px;
}

.Landing span a {
  color: white;
}
