.TopSellers {
  background-image: url("https://templatemo.com/templates/templatemo_577_liberty_market/assets/images/main-bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 120px 0px 90px;
  position: relative;
}

.sectionHeading {
  position: relative;
  z-index: 2;
  margin-top: 0px;
  margin-bottom: 80px;
  text-align: center;
}
.sectionHeading h2 {
  margin-top: 10px;
  line-height: 36px;
  font-size: 30px;
  font-weight: 700;
  text-transform: capitalize;
  color: #fff;
}
.sectionHeading h2::before {
  background-color: #fff;
  transform: translate(-50%, -1.6rem);
  left: 50%;
}

.Sellers {
  display: flex;
  flex-wrap: wrap;
  counter-reset: sellers;
  justify-content: center;
  column-gap: max(3rem, 5vw);
}

.Seller {
  display: flex;
  align-items: center;
  gap: 1.3rem;
  margin-bottom: 30px;
}
.Seller::before {
  counter-increment: sellers;
  content: counter(sellers) ".";
  color: #fff;
  font-weight: bold;
}

.Seller h6 {
  font-weight: bold;
  color: #fff;
  font-size: 20px;
}
.Seller h6 a {
  font-size: 14px;
  cursor: auto;
  color: #fff;
  font-weight: 400;
  margin-top: 5px;
  text-decoration: none;
}
