.Footer {
  background: linear-gradient(
    132deg,
    rgba(125, 90, 254, 1) 25%,
    rgba(160, 84, 244, 1) 100%
  );
  text-align: center;
  padding: 25px 0px;
}

.Footer p {
  color: #fff;
}
.Footer p a {
  color: rgba(214, 192, 238, 5);
  font-weight: 500;
  transition: color 0.3s;
  text-decoration: none;
}
.Footer p a:hover {
  color: white;
}
