.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: 400ms ease-out;
  z-index: 99;
}

@media (min-width: 55rem) {
  .Header {
    padding: 1rem 2rem;
  }

  .Scrolled {
    width: 80vw;
    left: auto;
  }
}

.Scrolled {
  position: fixed;
  border-radius: 50px;
  filter: opacity(0.85);
  font-weight: bold;
  left: 50%;
  transform: translateX(-50%);
}
